.nav {
  position: fixed;
  top: 0;
  left: 0;
  background:darkorange;
  min-height: 65px;
  width: 100%;
  text-align: center;
  -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25),
    0 10px 10px rgba(0, 0, 0, 0.22);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  color:black;
  font-size: 2em;
  font-weight: bold;
  z-index: 99;
  white-space: nowrap;
}

.nav a {
  color:black;
}

.nav a:hover {
  color: #fff;
  background-color: red !important;
}

.desc-normal {
  color: #fff;
}

.desc-correct {
  color: #0f0;
}

.desc-incorrect {
  color:black;
}



